<template>
  <div class="add-reward-post">
    <h2>Add Twitter Post for Rewards</h2>
    <p class="description">Add a Twitter post to the rewards system. Users will earn BITCAT power for interacting with it.</p>
    
    <div class="linked-account-info">
      <i class="fab fa-twitter"></i>
      <span> You can only add posts from your linked account: <strong>@{{ linkedTwitterUsername }}</strong></span>
    </div>
    
    <form @submit.prevent="submitPost" class="post-form">
      <div class="form-group">
        <label for="tweetId">Tweet URL</label>
        <div class="input-wrapper">
          <input 
            type="text" 
            id="tweetId" 
            v-model="tweetInput" 
            placeholder="Enter full Twitter URL"
            :disabled="loading"
            required
            autocomplete="off"
          />

          <button type="submit" class="submit-button" :disabled="loading">
            <i class="fas fa-plus-circle" v-if="!loading"></i>
            <i class="fas fa-spinner fa-spin" v-else></i>
            {{ loading ? 'Adding...' : 'Add Post' }}
          </button>
        </div>
        <small class="form-help">Example: https://x.com/{{ linkedTwitterUsername }}/status/1234567890</small>
      </div>
    </form>
    
    <div v-if="message" :class="['message', messageType]">
      <i :class="messageIcon"></i> {{ message }}
    </div>
  </div>
</template>

<script setup>
import { ref } from 'vue';
import axios from 'axios';

const props = defineProps({
  linkedTwitterUsername: {
    type: String,
    required: true
  }
});

const emit = defineEmits(['post-added']);

const tweetInput = ref('');
const loading = ref(false);
const message = ref('');
const messageType = ref('');
const messageIcon = ref('');

// Extract tweet ID from input (either ID or full URL)
const extractTweetId = (input) => {
  // Try to extract ID from URL
  const match = input.match(/twitter\.com\/[^/]+\/status\/(\d+)/i) || 
                input.match(/x\.com\/[^/]+\/status\/(\d+)/i);
  
  return match ? match[1] : null;
};

// Extract username from URL
const extractUsernameFromUrl = (input) => {
  const match = input.match(/(?:twitter|x)\.com\/([^/]+)\/status\/\d+/i);
  return match ? match[1] : null;
};

// Validate that the post belongs to the linked Twitter account
const validatePostOwnership = (input) => {
  // Extract username from URL
  const usernameFromUrl = extractUsernameFromUrl(input);
  
  if (!usernameFromUrl) {
    return { valid: false, error: `Please enter a full Twitter URL (e.g. https://x.com/${props.linkedTwitterUsername}/status/1234567890)` };
  }
  
  // Compare usernames (case insensitive)
  const linkedUsername = props.linkedTwitterUsername.toLowerCase();
  const urlUsername = usernameFromUrl.toLowerCase();
  
  if (linkedUsername !== urlUsername) {
    return { 
      valid: false, 
      error: `You can only add posts from your linked account (@${props.linkedTwitterUsername}). The provided URL is from @${usernameFromUrl}.`
    };
  }
  
  return { valid: true };
};

const submitPost = async () => {
  // Reset message
  message.value = '';
  
  // Validate post ownership first
  const ownership = validatePostOwnership(tweetInput.value);
  if (!ownership.valid) {
    message.value = ownership.error;
    messageType.value = 'error';
    messageIcon.value = 'fas fa-exclamation-circle';
    return;
  }
  
  // Extract tweet ID to verify URL is valid
  const tweetId = extractTweetId(tweetInput.value);
  
  if (!tweetId) {
    message.value = 'Invalid Tweet ID or URL format';
    messageType.value = 'error';
    messageIcon.value = 'fas fa-exclamation-circle';
    return;
  }
  
  try {
    loading.value = true;
    
    // Send the full URL, not just the tweet ID
    const response = await axios.post('/rewards/add-post', {
      tweetId: tweetInput.value
    });
    
    if (response.data.success) {
      message.value = 'Post added successfully!';
      messageType.value = 'success';
      messageIcon.value = 'fas fa-check-circle';
      tweetInput.value = ''; // Clear input
      emit('post-added'); // Notify parent component
    } else {
      message.value = response.data.message || 'Failed to add post';
      messageType.value = 'error';
      messageIcon.value = 'fas fa-exclamation-circle';
    }
  } catch (error) {
    console.error('Error adding post:', error);
    message.value = error.response?.data?.message || 'Error adding post. Please try again.';
    messageType.value = 'error';
    messageIcon.value = 'fas fa-exclamation-circle';
  } finally {
    loading.value = false;
  }
};
</script>

